<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="d-flex justify-content-end mt-2">
      <div class>
        <button class="btn btn-outline-primary mr-3" @click="openModal(true)">Build a new product</button>
      </div>
      <!-- <div class="">
      <button class="btn btn-outline-primary" @click="frontPage()">Front Page</button>
      </div>-->
    </div>

    <!-- 5true -->
    <table class="table mt-4">
      <thead>
        <th width="120">Types</th>
        <th width="180">Name</th>
        <th width="auto" class="text-right">Original price</th>
        <th width="auto" class="text-right">Price</th>
        <th width="auto">Enable</th>
        <th>edit</th>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">{{item.origin_price | currency}}</td>
          <td class="text-right">{{item.price | currency}}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success">Enable</span>
            <span v-else class="text-danger">Not enable</span>
            <!-- 2425 -->
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false ,item)">edit</button>
          </td>
          <!-- 28falseitem -->
        </tr>
      </tbody>
    </table>
    <!-- Bar -->
    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled': !pagination.has_pre}">
          <a
            class="page-link"
            href="#"
            aria-label="Previous"
            @click.prevent="getProducts(pagination.current_page - 1)"
          >
            <span aria-hidden="true">&laquo;</span>
            <span class="sr-only">Previous</span>
          </a>
          <!-- 38()-1() -->
        </li>
        <li
          class="page-item"
          v-for="page in pagination.total_pages"
          :key="page"
          :class="{'active':pagination.current_page === page}"
        >
          <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
        </li>
        <!-- 45() -->
        <!-- 44(active)active -->
        <li class="page-item" :class="{'disabled': !pagination.has_next}">
          <a
            class="page-link"
            href="#"
            aria-label="Next"
            @click.prevent="getProducts(pagination.current_page + 1)"
          >
            <span aria-hidden="true">&raquo;</span>
            <span class="sr-only">Next</span>
          </a>
          <!-- 48()+1() -->
        </li>
      </ul>
    </nav>

    <!-- productModal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>New product</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image">Image URL</label>
                  <input
                    v-model="tempProduct.imageUrl"
                    type="text"
                    class="form-control"
                    id="image"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <label for="customFile">
                    or upload image
                    <i class="fas fa-sync fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <!-- 87 -->
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  >
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  :src="tempProduct.imageUrl"
                  alt
                >
                <!-- 100 -->
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title">Title</label>
                  <input
                    v-model="tempProduct.title"
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder=""
                  >
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category">Type</label>
                    <input
                      v-model="tempProduct.category"
                      type="text"
                      class="form-control"
                      id="category"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="num">unit</label>
                    <input
                      v-model="tempProduct.num"
                      type="text"
                      class="form-control"
                      id="num"
                      placeholder=""
                    >
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price">OriginPrice</label>
                    <input
                      v-model="tempProduct.origin_price"
                      type="number"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                    >
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">Price</label>
                    <input
                      v-model="tempProduct.price"
                      type="number"
                      class="form-control"
                      id="price"
                      placeholder=""
                    >
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description">dascription</label>
                  <textarea
                    v-model="tempProduct.description"
                    type="text"
                    class="form-control"
                    id="description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content">content</label>
                  <textarea
                    v-model="tempProduct.content"
                    type="text"
                    class="form-control"
                    id="content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                    >
                    <!--  187,188 -->
                    <label class="form-check-label" for="is_enabled">enable</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">cancel</button>
            <button type="button" class="btn btn-primary" @click="updateProduct">comfirm</button>
            <!-- 202 -->
          </div>
        </div>
      </div>
    </div>
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>delete</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            comfirm delete
            <strong class="text-danger">{{ tempProduct.title }}</strong> Merchandise (cannot be recovered after deletion)
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">cancel</button>
            <button type="button" class="btn btn-danger">comfirm delete</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      products: [],
      //
      pagination: {},
      tempProduct: {},
      //
      isNew: false,
      //
      isLoading: false,
      //loding icon
      status: {
        fileUploading: false
      }
      //
    };
  },
  methods: {
    //page = 1ES61
    getProducts(page = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/products?page=${page}`;
      const vm = this;
      console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
        //271,272api
      });
    },
    updateProduct() {
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH}/admin/product`;
      let httpMethod = "post";
      const vm = this;
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${
          process.env.CUSTOMPATH
        }/admin/product/${vm.tempProduct.id}`;
        let httpMethod = "put";
        //putID
        //APIgetpostput(ID)
      }
      this.$http[httpMethod](api, { data: vm.tempProduct }).then(res => {
        console.log(res.data);
        if (res.data.success) {
          $("#productModal").modal("hide");
          vm.getProducts();
        } else {
          $("#productModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    },
    //modal(true() or false(falseitem)
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = Object.assign({}, item);
        //ES6item
        this.isNew = false;
      }
      $("#productModal").modal("show");
    },
    uploadFile() {
      console.log(this);
      const uploadedFile = this.$refs.files.files[0];
      //
      const vm = this;
      const formData = new FormData();
      //formData
      formData.append("file-to-upload", uploadedFile);
      //(,)
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/upload`;
      //icon(falsetrue)
      vm.status.fileUploading = true;
      this.$http
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(response => {
          console.log(response.data);
          if (response.data.success) {
            //icon(falsefalse)
            vm.status.fileUploading = false;
            //vm.tempProduct.imageUrl = response.data.imageUrl
            //console.log(vm.tempProduct)
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
            //vue-set(,,)
          } else {
            this.$bus.$emit("message:push", response.data.message, "danger");
          }
        });
    },
    frontPage() {
      this.$router.push("/HomePage");
    }
  },
  //Vue
  created() {
    this.getProducts();
  }
};
</script>


